import React from 'react';
import TypingEffect from './TypingEffect'; // adjust path as needed
import {
    FiDownload,
    FiHeart,
    FiSearch,
    FiShoppingCart,
    FiUpload,
} from 'react-icons/fi';
import { FaUserAlt, FaHeadphones, FaCube } from 'react-icons/fa';
import { BsTag, BsGrid } from 'react-icons/bs';
import { AiFillStar } from 'react-icons/ai';

const icons = [
    { icon: <FaCube className="text-purple-400" />, top: '5%', left: '8%' },
    { icon: <BsTag className="text-orange-400" />, top: '20%', left: '20%' },
    { icon: <FaUserAlt className="text-purple-500" />, top: '35%', left: '5%' },
    { icon: <FiDownload className="text-blue-300" />, top: '45%', left: '22%' },
    { icon: <FiHeart className="text-red-500" />, bottom: '25%', left: '8%' },
    { icon: <AiFillStar className="text-yellow-500" />, bottom: '10%', left: '25%' },
    { icon: <FaHeadphones className="text-gray-300" />, bottom: '6%', left: '40%' },
    { icon: <FiShoppingCart className="text-green-400" />, top: '10%', right: '10%' },
    { icon: <FiSearch className="text-blue-500" />, top: '40%', right: '20%' },
    { icon: <FiUpload className="text-teal-400" />, bottom: '35%', right: '10%' },
    { icon: <BsGrid className="text-orange-500" />, bottom: '15%', right: '25%' },
];

export default function HeroSection() {
    return (
        <section
            className="relative h-[90vh] bg-black text-white flex flex-col items-center justify-center text-center overflow-hidden px-6"
        >
            {/* Hero Text */}
            <div className="z-10 max-w-xl">
                <TypingEffect
                    texts={[
                        'Instant Payouts, Full Control, No Limits',
                        'Buy Once, Download Anytime, Keep Forever',
                    ]}
                    typingSpeed={100}
                    deletingSpeed={50}
                    pauseTime={1500}
                />
                <p className="text-gray-400 mb-6 pt-5">
                    Your one-stop digital platform for 3D models and digital creations. <br />
                    Join our community of creators and collectors today.
                </p>
                <button className="bg-gradient-to-r from-gray-700 to-gray-900 text-white px-6 py-3 rounded-full hover:scale-105 transition">
                    Explore all products
                </button>
            </div>

            {/* Icon Circles */}
            {icons.map((item, index) => (
                <div
                    key={index}
                    className="absolute rounded-full bg-white/10 flex items-center justify-center text-3xl shadow-xl backdrop-blur-md"
                    style={{ width: '72px', height: '72px', ...item }}
                >
                    {item.icon}
                </div>
            ))}
        </section>
    );
}
